import axios from 'axios' //axios
import {getCookie} from './cookie' //util.jsgetCookie

// axios 
axios.defaults.timeout = 500000;
axios.defaults.baseURL = 'http://192.168.1.109:8081/'; //

// http request Cookie
axios.interceptors.request.use(
  config => {
    //const token = getCookie('token'); //Cookie
    //config.data = JSON.stringify(config.data);
    let params=[];
    for(var key in config.data){
      if(config.data[key]!==null){
        params.push(key+'='+config.data[key])
      }
    }
    //alert(params.join('&'));
    config.data=params.join('&');
    config.headers = {
      'Content-Type':'application/x-www-form-urlencoded', //
    };
    /*if (token) {
      config.params = {'token': token} //
    }*/
    return config;
  },
  err => {
    return Promise.reject(err);
  }
);


